.auth {
    margin: 3rem auto;
    width: 95%;
    max-width: 25rem;
    border-radius: 6px;
    background-color: rgb(245, 250, 250);
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    padding: 1rem;
    text-align: center;
  }
  
  .auth h1 {
    text-align: center;
    color: black;
  }
  .control {
    margin-bottom: 0.5rem;
  }
  
  .control label {
    display: block;
    color: black;
    font-weight: bold;
    margin-bottom: 0.5rem;
    text-align: left;
    
  }
  
  .control input {
    font: inherit;
    background-color: rgb(224, 235, 235);
    color: rgb(92, 95, 95);;
    border-radius: 4px;
    border: 1px solid white;
    width: 100%;
    text-align: left;
    padding: 0.25rem;
  }

  .actions {
    margin-top: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .actions button {
    cursor: pointer;
    font: inherit;
    color: white;
    background-color: teal;
    border: 1px solid teal;
    border-radius: 4px;
    padding: 0.5rem 2.5rem;
  }
  
  .actions button:hover {
    background-color: rgb(2, 43, 43);
    border-color: rgb(2, 43, 43);
  }